
.container {
  position: relative;
  width: 100%;
}

.controls {
  display: block;
}

.slides {
  position: relative;
  margin: 0px;
  padding: 0px;
  height: 500px;
  list-style-type: none;

  &__item {
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 1;
    box-sizing: border-box;
    padding: 40px;
    width: 100%;
    height: 100%;
    background-color: #333;
    background-position: 50% 50%;
    background-size: cover;
    color: #fff;
    font-size: 40px;
    opacity: 0;
    transition: opacity 1s;

    &.active {
      z-index: 2;
      opacity: 1;
    }

    &:nth-of-type(1) {
      background-color: red;
      background-image: url('../img/slide1.jpg');
    }

    &:nth-of-type(2) {
      background-color: orange;
      background-image: url('../img/slide2.jpg');
    }

    &:nth-of-type(3) {
      background-color: green;
      background-image: url('../img/slide3.jpg');
    }
  }
}

.controls {
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 10;
  width: 100%;
  height: 100%;

  &__item {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    min-width: 50px;
    width: 10%;
    height: 100%;
    background-color: rgba(#000000, 0.35);
    cursor: pointer;
  }

  &__next {
    top: 0;
    right: 0;
    background: linear-gradient(to left, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0) 100%);

    &:hover,
    &:focus {
      background: linear-gradient(to left, rgba(0,0,0,0.85) 0%, rgba(0,0,0,0) 100%);
    }
  }

  &__prev {
    top: 0;
    left: 0;
    background: linear-gradient(to right, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0) 100%);

    &:hover,
    &:focus {
      background: linear-gradient(to right, rgba(0,0,0,0.85) 0%, rgba(0,0,0,0) 100%);
    }
  }

  img {
    width: 50%;
    filter: brightness(500%);
  }
}

.indicators {
  position: absolute;
  bottom: 15px;
  z-index: 20;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;

  span {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 5px;
    width: 30px;
    height: 30px;
    border-radius: 15px;
    background-color: #ffffff;
    box-shadow: 0 0 15px #000000;
    color: #222222;
    text-align: center;
    cursor: pointer;
  }

  &__item {
    &.active {
      background-color: #222222;
      box-shadow: 0 0 15px #ffffff;
      color: #ffffff;
    }
  }
}